import types from './types'

// actions
const actions = {
    BooleanIsIndex: ({ commit }, boolean) => {
        commit(types.IS_INDEX, boolean);
    },
    BooleanBannerShow: ({ commit }, boolean) => {
        commit(types.BANNER_SHOW, boolean);
    },
    getIndexTitleData: ({ commit }, data) => {
        commit(types.INDEX_TITLE_DATA, data);
    },
    getRegistrationSite: ({ commit }, data) => {
        commit(types.REGISTRATION_SITE, data);
    },
    getExaminatioPlanResource: ({ commit }, data) => {
        commit(types.EXAMINATIO_PLAN_RESOURCE, data);
    },
    getZSJZ: ({ commit }, data) => {
        commit(types.ZSJZ, data);
    },
    getXLZX: ({ commit }, data) => {
        commit(types.XLZX, data);
    },
    getZSYX: ({ commit }, data) => {
        commit(types.ZSYX, data);
    },
    getACTIVE_TYPE: ({ commit }, type) => {
        commit(types.ACTIVE_TYPE, type);
    },
    getZSZY: ({ commit }, type) => {
        commit(types.ZSZY, type);
    },
    USER_FORM: ({ commit }, type) => {
        commit(types.USER_FORM, type);
    },
    HOT_MAJOR: ({ commit }, data) => {
        commit(types.HOT_MAJOR, data);
    },
};

export default actions
