import UniversityList from "../components/UniversityList";
import ButtonFloat from "../components/ButtonFloat";
import SchoolDec from "../components/SchoolDec";
import Streamer from "../components/Streamer";
import IndexForm from "../components/IndexForm";

export default {
  name: "Home",
  data() {
    return {
      bannerImgUrl: [
        require("../assets/images/banner1.png"),
        require("../assets/images/banner2.png"),
        require("../assets/images/banner3.png"),
        require("../assets/images/banner4.png")
      ],

      btn_arr: [
        {
          type: "1",
          typeName: "招生简章"
        },
        {
          type: "2",
          typeName: "考试安排"
        },
        {
          type: "3",
          typeName: "复习资料"
        },
        {
          type: "4",
          typeName: "毕业论文"
        },
        {
          type: "5",
          typeName: "备考经验"
        },
        {
          type: "6",
          typeName: "资讯动态"
        }
      ],
      school_dec: [],
      streamerArr: [
        {
          url: require("../assets/images/ad_02.png"),
          link: "home"
        },
        {
          url: require("../assets/images/ad_05.png"),
          link: "news"
        }
      ]
    };
  },
  mounted() {
    //通知公告
    this.$http("getExaminatioPlanResource", "get").then(res => {
      this.$store.dispatch("getExaminatioPlanResource", res);
    });
    //得到招生简章
    this.$http("getBusinessPromotionBrochuresResource", "get").then(res => {
      this.$store.dispatch("getZSJZ", res);
      //政策动态
      this.getTypeEvent2(1);
    });
    //学历资讯
    this.$http("getExaminatioConsult", "get").then(res => {
      this.$store.dispatch("getXLZX", res);
    });
    //招生院校
    this.$http("getUniversityResource", "get").then(res => {
      this.$store.dispatch("getZSYX", res);
    });
    //招生专业
    this.$http("getProfessionalResource", "get").then(res => {
      this.$store.dispatch("getZSZY", res);
    });
  },
  computed: {
    pushExaminatioPlanResource() {
      return this.$store.getters.ExaminatioPlanResource;
    },
    pushZSJZ() {
      return this.$store.getters.ZSJZ_data;
    },
    pushXLZX() {
      return this.$store.getters.XLZX_data;
    },
    pushZSYX() {
      return this.$store.getters.ZSYX_data;
    },
    pushZSZY() {
      return this.$store.getters.ZSZY_data;
    }
  },
  methods: {
    //政策动态 tabs 切换
    getTypeEvent2(data) {
      console.log(data);
      console.log(this.$store.getters.ZSJZ_data.listsArr);
      if (Number(data) == 1) {
        this.school_dec = this.$store.getters.ZSJZ_data.listsArr;
      } else if (Number(data) == 2) {
        this.school_dec = this.$store.getters.ExaminatioPlanResource.listsArr;
      } else if (Number(data) == 3) {
        this.school_dec = this.$store.getters.ZSJZ_data.listsArr;
      }
    }
  },
  components: {
    Title,
    NewsList,
    EnrollmentGuide,
    InfoShow,
    UniversityList,
    ButtonFloat,
    SchoolDec,
    Streamer,
    IndexForm
  }
};
</script>

