<template>
  <div class="streamer" :style="styleObj">
    <router-link :to="'/'+streamer.link">
      <div class="link_wechat_inner">
        <img :src="streamer.url" alt />
      </div>
    </router-link>
  </div>
</template>

<script>
export default {
  //横幅
  name: "Streamer",
  props: {
    streamer: Object,
    top: String,
    bottom: String
  },
  data() {
    return {};
  },
  computed: {
    styleObj: function() {
      return {
        marginTop: (this.top == null ? 0 : this.top) + "px",
        marginBottom: (this.bottom == null ? 0 : this.bottom) + "px"
      };
    }
  }
};
</script>


<style lang="scss" scoped>
div.link_wechat_inner {
  width: 1200px;
  margin: 0 auto;
  font-size: 0;

  a {
    display: block;
    width: 100%;
    font-size: 0;
    img {
      width: 100%;
    }
  }
}
</style>