<template>
  <ul class="school_tabs btnfloat">
    <li
      v-for="(item,index) in btn_arr"
      :key="index"
      :class="{'active': index == isActive }"
      @click="changeActive(index,item.type)"
    >
      <a href="javascript:void(0)">{{item.typeName}}</a>
    </li>
  </ul>
</template>

<script>
export default {
  name: "ButtonFloat",
  data() {
    return {
      isActive: 0
    };
  },
  props: {
    btn_arr: Array
  },
  methods: {
    changeActive(index, type) {
      this.isActive = index;
      this.$emit("getType", type);
    }
  }
};
</script>

