import types from './types'

// mutations
const mutations = {
    [types.IS_INDEX](state, boolean) {
        state.isIndex = boolean
    },
    [types.BANNER_SHOW](state, boolean) {
        state.bannerShow = boolean
    },
    [types.INDEX_TITLE_DATA](state, data) {
        state.indexTitleData = data
    },
    [types.REGISTRATION_SITE](state, data) {
        state.registrationSite = data
    },
    [types.EXAMINATIO_PLAN_RESOURCE](state, data) {
        state.ExaminatioPlanResource = data
    },
    [types.ZSJZ](state, data) {
        state.ZSJZ_data = data
    },
    [types.XLZX](state, data) {
        state.XLZX_data = data
    },
    [types.ZSYX](state, data) {
        state.ZSYX_data = data
    },
    [types.ACTIVE_TYPE](state, type) {
        state.active_type = type
    },
    [types.ZSZY](state, type) {
        state.ZSZY_data = type
    },
    [types.USER_FORM](state, type) {
        state.USER_FORM = type
    },
    [types.HOT_MAJOR](state, data) {
        state.HOT_MAJOR = data
    },
};


export default mutations

