import Vue from 'vue'
import store from '../store/store'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'Home',
        component: Home
    },
    {
        path: '/news',
        name: 'NewsLetter',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/NewsLetter.vue')
    },
    {
        path: '/detail',
        name: 'NewsDetail',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/NewsDetail.vue')
    },
    {
        path: '/place',
        name: 'Place',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/Place.vue')
    },
    {
        path: '/other_place',
        name: 'OtherPlace',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/OtherPlace.vue')
    },
    {
        path: '/hot_major',
        name: 'HotMajor',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/HotMajor.vue')
    },
    { path: '*', redirect: '/' }
    // <router-link to="/">Home</router-link> |
    // <router-link to="/about">About</router-link>
]

const router = new VueRouter({
    // mode: 'history',
    routes
})
router.beforeEach((to, from, next) => {
    if (to.name !== 'Home') {
        store.dispatch("BooleanIsIndex", false);
    } else {
        store.dispatch("BooleanIsIndex", true);
    }

    if (to.name == 'OtherPlace') {
        store.dispatch("BooleanBannerShow", false);
    } else {
        store.dispatch("BooleanBannerShow", true);
    }
    // 如果用户未能验证身份，则 `next` 会被调用两次
    next()

})
export default router
