<template>
  <div class="form" v-if="pushUSER_FORM">
    <h3 class="form_title">2020年10月成考报名系统</h3>

    <p class="form_num">
      目前已经有
      <span>{{pushUSER_FORM.bMnumber}}</span>人报名
    </p>

    <ul class="form_list">
      <li>
        <!-- 院校选择 -->
        <el-select
          :class="{'active':error_school}"
          v-model="school_select"
          clearable
          @change="sendSchool(school_select)"
          placeholder="请选择您的报考学校"
          size="medium"
          :style="{'width':'100%','height': '40px'}"
          id="school"
        >
          <el-option
            v-for="item in pushUSER_FORM.university"
            :key="item.sCHOOLNAME"
            :label="item.sCHOOLNAME"
            :value="item.sCHOOLID"
          ></el-option>
        </el-select>
      </li>
      <li>
        <!-- 专业选择  -->
        <el-select
          @change="sendMajor"
          :class="{'active':error_major}"
          v-model="major_select"
          clearable
          placeholder="请选择您的报考专业"
          size="medium"
          :style="{'width':'100%','height': '40px'}"
          id="major"
        >
          <el-option
            v-for="item in major"
            :key="item.professionalNAME"
            :label="item.professionalNAME"
            :value="item.professionalID"
          ></el-option>
        </el-select>
      </li>
      <li>
        <!-- 提交姓名 -->
        <el-input
          @blur="sendUserName"
          :class="{'active':error_user_name}"
          placeholder="您的姓名"
          v-model="user_name"
          clearable
          id="name"
        ></el-input>
      </li>
      <li>
        <!-- 提交手机号 -->
        <el-input
          @blur="sendUserPhone"
          :class="{'active':error_user_phone}"
          placeholder="您的联系电话"
          v-model="user_phone"
          clearable
          id="phone"
        ></el-input>
        <p class="phone_errinfo">{{error_user_phone}}</p>
      </li>
    </ul>
    <button class="submit_info" @click="sendResult">提交报名信息</button>
  </div>
</template>

<script>
export default {
  name: "IndexForm",
  data() {
    return {
      school_select: "",
      major: null,
      major_select: "",
      user_name: "",
      user_phone: "",
      error_school: false,
      error_major: false,
      error_user_name: false,
      error_user_phone: ""
    };
  },
  mounted() {
    //得到下拉框信息
    this.$http("getSchoolAndPersional", "get").then(res => {
      this.$store.dispatch("USER_FORM", res);
    });
  },
  computed: {
    pushUSER_FORM() {
      return this.$store.getters.USER_FORM;
    }
  },
  methods: {
    sendSchool(schoolID) {
      this.major_select = "";
      this.$http(
        "getProfessionalResourceBySchool",
        {
          UniversityId: schoolID
        },
        "post"
      )
        .then(res => {
          this.major = res;
          this.school_select == ""
            ? (this.error_school = true)
            : (this.error_school = false);
        })
        .catch(res => {
          console.log(res);
        });
    },
    sendMajor() {
      this.major_select == ""
        ? (this.error_major = true)
        : (this.error_major = false);
    },
    sendUserName() {
      this.user_name == ""
        ? (this.error_user_name = true)
        : (this.error_user_name = false);
    },
    sendUserPhone() {
      this.validatePhone();
    },
    validatePhone() {
      // 验证手机号码
      if (!this.user_phone) {
        this.error_user_phone = "固定电话不能为空";
        return false;
      } else if (
        !/^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,11}$/.test(this.user_phone)
      ) {
        this.error_user_phone = "固定电话有误，请检查";
        return false;
      } else {
        this.error_user_phone = "";
        return true;
      }
    },
    sendResult() {
      if (this.school_select == "") {
        this.error_school = true;
        return;
      } else {
        this.error_school = false;
      }
      if (this.major_select == "") {
        this.error_major = true;
        return;
      } else {
        this.error_major = false;
      }
      if (this.user_name == "") {
        this.error_user_name = true;
        return;
      } else {
        this.error_user_name = false;
      }
      //验证手机号之后 发送result api
      if (this.validatePhone()) {
        this.$http(
          "insertEaminatioMessag",
          {
            personName: this.user_name,
            phonenumber: this.user_phone,
            selectPerfessionalID: this.major_select,
            selectSchoolID: this.school_select
          },
          "post"
        )
          .then(res => {
            // console.log(res);
            this.$message({
              message: "恭喜你报名成功，稍后会有老师联系您！",
              type: "success"
            });
          })
          .catch(res => {
            // console.log(res);
            this.$message({
              message: "错了哦，请您换个手机号！",
              type: "error"
            });
          });
      }
    }
  }
};
</script>

