<template>
  <div class="university_box">
    <div class="top">
      <ButtonFloat :btn_arr="university_data.school_tabs" @getType="getTypeEvent" />

      <a href="#">更多院校</a>
    </div>

    <ul class="university_items">
      <li v-for="(list,index) in typeFilter" :key="index">
        <img :src="list.imgUrl" alt />
        <h3>{{list.school_title}}</h3>
        <ol>
          <li v-for="tab in list.school_dec" :key="tab">{{tab}}</li>
        </ol>
      </li>
    </ul>
  </div>
</template>

<script>
import ButtonFloat from "./ButtonFloat";
export default {
  name: "UniversityList",
  data() {
    return {
      type: "1"
    };
  },
  props: {
    university_data: Object
  },
  components: {
    ButtonFloat
  },
  methods: {
    getTypeEvent(data) {
      console.log(data);
      this.type = data;
    }
  },
  computed: {
    typeFilter() {
      var that = this;
      return this.university_data.school_banner.filter(item => {
        return item.school_type == this.type;
      });
    }
  }
};
</script>

